@import-normalize; /* bring in normalize.css styles */

body {
  margin: 0;
  font-family: 'Hanken Grotesk', Arial, sans-serif;
  background-color: #262626;
  color: #d9d9d9;
  padding: 20px;
  /* height: 100vh; */
  display: flex;
  justify-content: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  text-align: center;
}

header > h1 {
  font-size: 1.3em;
  color: #f9f4da;
  font-weight: 500;
}

header > p {
  font-size: 0.875rem;
  max-width: 350px;
  color: #8e8e8e;
  margin-left: auto;
  margin-right: auto;
}

.game-status {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #f9f4da;
  border-radius: 4px;
  margin-block: 30px;
  height: 61px;
}

.game-status.won {
  background-color: #10a95b;
}

.game-status.lost {
  background-color: #ba2a2a;
}

.game-status.farewell {
  background-color: #7a5ea7;
  border: 1px dashed #323232;
}

.game-status > p {
  margin: 5px;
}

.game-status > p.farewell-mesage {
  font-style: italic;
  font-weight: 400;
}

.game-status > h2 {
  font-size: 1.25rem;
  margin: 5px;
}

.language-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  justify-content: center;
  max-width: 350px;
  margin: 0 auto;
  margin-bottom: 36px;
}

.chip {
  border-radius: 3px;
  padding: 4.5px;
  position: relative;
}

.chip.lost::before {
  content: 'ðŸ’€';
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  font-size: 0.85rem;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.word {
  display: flex;
  justify-content: center;
  gap: 2px;
  margin-bottom: 20px;
}

.word > span {
  height: 40px;
  width: 40px;
  background-color: #323232;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.125rem;
  border-bottom: 1px solid #f9f4da;
}

.word > span.missed-letter {
  color: #ec5d49;
}

.keyboard {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  max-width: 450px;
  margin-bottom: 36px;
}

.keyboard > button {
  height: 35px;
  width: 35px;
  cursor: pointer;
  background-color: #fcba29;
  border: 1px solid #d7d7d7;
  border-radius: 3px;
}

.keyboard > button.correct {
  background-color: #10a95b;
}

.keyboard > button.wrong {
  background-color: #ec5d49;
}
.keyboard > button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

button.new-game {
  background-color: #11b5e5;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  width: 225px;
  height: 40px;
  padding: 6px 12px;
  display: block;
  margin-inline: auto;
  cursor: pointer;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
